
import { Options, Vue } from "vue-class-component";
import store from "@/store/index";
import { BaseService } from "@/services/base-service";
import { IListItem } from "@/domain/IListItem";

@Options({
    components: {},
    props: {},
})
export default class ListElementsIndex extends Vue {
    listitems: IListItem[] | null = null;

    get apiKey(): string {
        return store.state.apiKey;
    }

    async checkboxClicked(item: IListItem): Promise<void> {
        const objToSave: IListItem = {
            id: item.id!,
            description: item.description!,
            completed: !item.completed,
        };
        const service = new BaseService<IListItem>(
            "https://taltech.akaver.com/api/v1/ListItems",
            "?apiKey=" + this.apiKey
        );
        service.put(item.id!, objToSave).then((statusCode) => {
            if (statusCode.statusCode >= 200 && statusCode.statusCode < 300) {
                this.$router.push("/");
            }
        });
    }

    async deleteClicked(id: string): Promise<void> {
        const service = new BaseService<IListItem>(
            "https://taltech.akaver.com/api/v1/ListItems",
            "?apiKey=" + this.apiKey
        );
        service.delete(id).then(() => {
            window.location.reload();
        });
    }

    async filterButtonClicked(queryParam: string): Promise<void> {
        const service = new BaseService<IListItem>(
            "https://taltech.akaver.com/api/v1/ListItems",
            "?apiKey=" + this.apiKey + queryParam
        );
        service.getAll().then((data) => {
            this.listitems = data.data!;
        });
    }

    mounted(): void {
        const service = new BaseService<IListItem>(
            "https://taltech.akaver.com/api/v1/ListItems",
            "?apiKey=" + this.apiKey
        );
        service.getAll().then((data) => {
            this.listitems = data.data!;
        });
    }
}
